<template>
  <div class="search">
    <BgMask color="rgb(204 205 209)" />
    <!-- 头部搜索框 -->
    <header :class="{ whiteBg: searchState }">
      <img src="../../public/img/leftArrowhead.svg" @click="$router.back()" />
      <van-search
        v-model="value"
        show-action
        placeholder="请输入搜索关键词"
        @search="onSearch(value)"
      >
        <template #action>
          <div @click="onSearch(value)" v-if="!searchState || value == ''">
            搜索
          </div>
          <div @click="onCancel" v-else>取消</div>
        </template>
      </van-search>
    </header>

    <template v-if="!searchState">
      <!-- 热搜书籍 -->
      <div class="hotSearch">
        <h3>热搜书籍<van-icon name="fire-o" color="#ee0a24" /></h3>
        <ul v-if="!hotsearchState">
          <li
            v-for="(item, index) in hotWords"
            :key="index"
            @click="onSearch(item.word)"
          >
            <em>{{ index + 1 }}</em>
            <span>{{ item.word }}</span>
          </li>
        </ul>
        <div class="hotsearch-wait" v-else>
          <van-loading size="24px">热词获取中...</van-loading>
        </div>
      </div>
      <!-- 搜索历史 -->
      <div class="history">
        <h3>
          搜索历史
          <img src="../../public/img/rubbish.svg" @click="historyWords = []" />
        </h3>
        <ol>
          <li
            v-for="(item, index) in historyWords"
            :key="index"
            @click="onSearch(item)"
          >
            {{ item }}
          </li>
        </ol>
      </div>
    </template>
    <template v-else>
      <div class="search-result-box">
        <ul v-if="!searchWrong">
          <BookItemTypeTwo
            v-for="item in searchBooks.slice(0, 20)"
            :item="item"
            :key="item._id"
            :loading="!searchState"
          />
        </ul>
        <div class="searchFalse" v-else>
          <img src="../../public/img/searchFalse.svg" />
          <p>搜索失败</p>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import BgMask from "@/components/BgMask.vue";
import BookItemTypeTwo from "@/components/BookItemTypeTwo.vue";
import { Toast } from "vant";
export default {
  components: {
    BookItemTypeTwo,
    BgMask,
  },
  data() {
    return {
      value: "",
      hotWords: [],
      searchBooks: [],
      hotsearchState: false,
      searchState: false, //当前是否真正搜索书本状态
      searchWrong: false,
      historyWords:
        JSON.parse(window.localStorage.getItem("historyWords")) || [],
    };
  },
  watch: {
    historyWords(n) {
      window.localStorage.setItem("historyWords", JSON.stringify(n));
    },
    value(n) {
      if (n == "" || n == null || n == undefined) {
        this.searchState = false;
      }
    },
  },
  created() {
    this.hotsearchState = true;
    this.hotSearchWords();
  },
  methods: {
    // 查询input框内的内容
    onSearch(val) {
      this.value = val;
      if (this.value == "" || this.value == undefined || this.value == null) {
        console.log("无内容");
        return;
      }
      // 处理相同的历史数组元素
      let words = [...this.historyWords];
      words.unshift(val);
      const s = new Set(words);
      this.historyWords = [...s];
      // 设置当前的搜索状态
      this.searchState = false;
      this.searchWrong = false;
      Toast("搜索中:" + val);
      this.axios
        .get("/search?keyword=" + val)
        .then((res) => {
          // console.log(res);
          if (res.data.books.length == 0) {
            this.searchWrong = true;
            this.searchState = true;
            return;
          }
          this.searchBooks = res.data.books;
        })
        .finally(() => {
          this.searchState = true;
        });
    },
    onCancel() {
      this.value = "";
      this.searchBooks = [];
      this.searchState = false;
    },
    hotSearchWords() {
      this.axios
        .get("/search-hotwords")
        .then((res) => {
          // console.log(res.data.searchHotWords);
          this.hotWords = res.data.searchHotWords.slice(0, 8);
        })
        .finally(() => {
          this.hotsearchState = false;
        });
    },
  },
};
</script>
<style lang="less">
.contain {
  margin: 70px auto;
  padding: 10px 5px;
  width: 90vw;
  min-height: 150px;
  border-radius: 10px;
  background: #fff;
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.15);
}
.search {
  header {
    z-index: 3;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    display: flex;
    align-items: center;
    img {
      width: 25px;
      height: 25px;
      margin: 0 5px;
    }
    .van-search--show-action {
      padding-left: 0;
    }
    .van-search {
      flex: 1;
      background: transparent;
      .van-search__content {
        border-radius: 15px;
      }
    }
    .van-search__action {
      color: #fff;
      &:active {
        background: transparent;
      }
    }
    &.whiteBg {
      background: #fff;
      .van-search__action {
        color: #000;
      }
    }
  }

  .hotSearch {
    .contain();
    h3 {
      font-size: 12px;
      font-weight: 600;
      i {
        margin-left: 5px;
        vertical-align: middle;
      }
    }
    ul {
      margin-top: 10px;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      li {
        width: 50%;
        height: 30px;
        line-height: 30px;
        em {
          display: inline-block;
          width: 22px;
          height: 22px;
          border-radius: 50%;
          line-height: 22px;
          text-align: center;
          background: #c5c5c5;
          font-size: 12px;
          color: #fff;
          margin: 4px 0;
        }
        &:nth-of-type(1) {
          em {
            background: red;
          }
        }
        &:nth-of-type(2) {
          em {
            background: orange;
          }
        }
        &:nth-of-type(3) {
          em {
            background: #ffeb3b;
          }
        }
        span {
          margin-left: 10px;
          color: #666;
          font-size: 12px;
        }
      }
    }
    .hotsearch-wait {
      text-align: center;
      font-size: 12px;
    }
  }
  .history {
    .contain ();
    h3 {
      font-size: 12px;
      font-weight: 600;
      img {
        width: 16px;
        height: 16px;
        vertical-align: middle;
        float: right;
      }
    }
    ol {
      margin-top: 10px;
      li {
        padding: 1px 5px;
        margin-right: 10px;
        margin-bottom: 10px;
        background: #dadada;
        border-radius: 7px;
        font-size: 12px;
        float: left;
      }
    }
  }
  .search-result-box {
    margin: 54px auto;
    width: 100vw;
    min-height: 300px;
    background: #fff;
    padding: 0 10px;
    box-sizing: border-box;
    .searchFalse {
      margin-top: 20px;
      text-align: center;
      img {
        width: 50px;
      }
    }
  }
}
</style>